import React from 'react'

const ServiceImage = ({image,imageName,description}) => {
  return (
    <section className="d421 wow fadeInUp" data-wow-delay="0.1s">
    <figure><img src={image} alt=""/></figure>
    <figcaption>
        <h4>{imageName}</h4><br/>
        <p>{description}</p>
    </figcaption><br/>
    <div className="container">
        <div className="ccc1 fa fa-arrow-right" style={{color:"white"}} >
            <div className="ccc2">
                 <h5>Read more</h5>
            </div>
        </div>
    </div>
</section>
  )
}

export default ServiceImage