import React from 'react'

const TeamMember = ({img, TeamMember, Designation}) => {
  return (
   <>
    <section className="d821 wow fadeInUp" data-wow-delay="0.1s">
                        <section className="img"><img src= { img } alt=""/></section>
                        <section className="profile">
                            <h4>{Designation}</h4>
                            <p>{TeamMember}</p>
                            <div className="container">
                                <div className="ccc11p fa fa-share" style={{color:"white"}}>
                                    <div className="ccc21p">
                                         <a href=""><i className="fab fa-facebook"style={{color:"white"}}></i></a>
                                         <a href=""><i className="fab fa-twitter"style={{color:"white"}}></i></a>
                                         <a href=""><i className="fab fa-instagram"style={{color:"white"}}></i></a>
                                    </div>
                                </div>
                            </div>
                        </section> <br/>
                    </section>
   </>
  )
}

export default TeamMember